import React, { useEffect, useState, useRef } from "react";
import { Link } from "react-router-dom";
import Navbar from "./Navbar";
import LanguageSelect from "./LangugeSelect";

function Header() {
  const [isSticky, setIsSticky] = useState(false);
  const ticking = useRef(false);

  const handleScroll = () => {
    if (!ticking.current) {
      window.requestAnimationFrame(() => {
        const scrollY = window.scrollY;
        setIsSticky(scrollY > 0);
        ticking.current = false;
      });
      ticking.current = true;
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <div className={`header ${isSticky ? "sticky" : ""}`}>
      <div className="container">
        <h1 className="logo">
          <Link to="/">
            {/*?xml version="1.0" encoding="UTF-8"?*/}
            <svg
              id="Layer_1"
              data-name="Layer 1"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 521.72 52.07"
              width={"200px"}
            >
              <defs>
                <style>
                  {`
                  .cls-1 {
                    fill: #001a54;
                  }
                  `}
                </style>
              </defs>
              <path
                className="cls-1"
                d="M72.6,3.99c2.72,2.66,4.07,5.89,4.07,9.7v24.69c0,3.8-1.36,7.04-4.07,9.7-2.72,2.66-6,3.99-9.86,3.99H13.77c-3.8,0-7.05-1.33-9.74-3.99-2.69-2.66-4.03-5.89-4.03-9.7V13.69c0-3.8,1.34-7.03,4.03-9.7C6.72,1.33,9.97,0,13.77,0h48.97c3.86,0,7.14,1.33,9.86,3.99ZM56.87,12.96H19.72v26.15h37.16V12.96Z"
              />
              <path
                className="cls-1"
                d="M142.26,12.96h-37.32v39.11h-19.64l-.08-30.8v-8.31l.08-12.96h62.99c3.75,0,6.97,1.33,9.65,3.99,2.69,2.66,4.03,5.89,4.03,9.7v38.38h-19.88l.16-39.11Z"
              />
              <path
                className="cls-1"
                d="M246.88,0v21.1c-.27,3.04-1.67,5.87-4.2,8.47-2.53,2.61-5.77,3.91-9.74,3.91h-13.53v18.58h-21.35v-18.74l-13.36.08c-4.07,0-7.51-1.33-10.31-3.99-2.8-2.66-4.2-5.87-4.2-9.62V0h21.02v21.27h34.55V0h21.1Z"
              />
              <path
                className="cls-1"
                d="M274.5,39.11h37.24l19.47-.08.24,13.04h-62.99c-3.75,0-6.97-1.33-9.65-3.99-2.69-2.66-4.03-5.89-4.03-9.7V13.69c0-3.8,1.34-7.03,4.03-9.7,2.69-2.66,5.91-3.99,9.65-3.99h62.99l-.24,12.96h-56.71v26.15Z"
              />
              <path
                className="cls-1"
                d="M412.45,3.99c2.72,2.66,4.07,5.89,4.07,9.7v24.69c0,3.8-1.36,7.04-4.07,9.7-2.72,2.66-6,3.99-9.86,3.99h-48.97c-3.8,0-7.05-1.33-9.74-3.99-2.69-2.66-4.03-5.89-4.03-9.7V13.69c0-3.8,1.34-7.03,4.03-9.7,2.69-2.66,5.93-3.99,9.74-3.99h48.97c3.86,0,7.14,1.33,9.86,3.99ZM396.73,12.96h-37.15v26.15h37.15V12.96Z"
              />
              <path
                className="cls-1"
                d="M517.72,3.99c2.66,2.66,3.99,5.89,3.99,9.69v38.35h-19.74V12.92h-18.76v39.11h-19.69V12.92h-18.76v39.11h-19.69V0h83.03c3.75,0,6.95,1.33,9.61,3.99Z"
              />
            </svg>
          </Link>
        </h1>
        <div className="nav-area">
          <Navbar />
          <LanguageSelect />
          <button type="button" className="contact">
            CONTACT
          </button>
        </div>
      </div>
    </div>
  );
}

export default Header;
